import React from 'react';
import PropTypes from 'prop-types';
import { withEpisode } from './Episode.Context';
import Button from '../Button';

import styles from './Episode.Choices.module.scss';
import Card from '../Card/Card';

const Choices = ({
  getStepInfo,
  makeStep,
  progress,
}) => {
  const step = getStepInfo();
  const handleChoice = (choice) => () => {
    makeStep(choice.target, choice);
  };

  return (
    <Card className={styles.root} hide={progress <= 1}>
      <div
        className={[
          styles.inner,
          progress <= 1 && styles.hide,
        ].filter(Boolean).join(' ')}
      >
        {step && step.choices.map((choice) => (
          <Button
            key={choice.target}
            onClick={handleChoice(choice)}
            className={styles.button}
          >
            {choice.title}
          </Button>
        ))}
      </div>
    </Card>
  );
};

Choices.propTypes = {
  getStepInfo: PropTypes.func.isRequired,
  makeStep: PropTypes.func.isRequired,
  progress: PropTypes.number.isRequired,
};

Choices.displayName = 'MKEpisode.Choices';

export default Choices;

export const Connected = withEpisode(Choices);
