import React from 'react';
import { withEpisode } from './Episode.Context';

import styles from './Episode.History.module.scss';

const History = withEpisode(({
  getStepInfo,
  steps,
}) => (
  <div className={styles.root}>
    <div>Episode history:</div>

    {steps.map((stepId) => {
      const step = getStepInfo(stepId);

      return (step && (
        <div key={stepId}>{step.title}</div>
      )) || null;
    })}
  </div>
));

History.displayName = 'MKEpisode.History';

export default History;
